import {put, call, take} from 'redux-saga/effects'
import {setImage} from '../ducks/carousel'
import {SHOW_PREVIOUS_IMAGE, SHOW_NEXT_IMAGE} from '../ducks/carousel'

function * setImageFlow () {
  while (true) {
    const action = yield take([SHOW_NEXT_IMAGE, SHOW_PREVIOUS_IMAGE])
    let {currentImage, imagesLength} = action.payload

    switch (action.type) {
      case SHOW_PREVIOUS_IMAGE:
        currentImage = currentImage > 0 ? currentImage - 1 : imagesLength - 1
        break

      case SHOW_NEXT_IMAGE:
        currentImage = currentImage >= imagesLength - 1 ? 0 : currentImage + 1
        break
    }
    yield put(setImage(currentImage))
  }
}

function * carouselSaga () {
  yield call(setImageFlow)
}

export default carouselSaga



