import 'babel-polyfill'
import React from 'react'
import {render} from 'react-dom'
import {Provider} from 'react-redux'
import TravelabJmApp from './environment/travelab-jm'
import configureStore from './environment/travelab-jm/store/configureStore'
import travelabJmAppStyles from './environment/travelab-jm/styles/app.css'

const store = configureStore()

render(
  <Provider store={store}>
    <div className={travelabJmAppStyles.app}>
      <TravelabJmApp />
    </div>
  </Provider>,
  document.querySelector('#root')
)



