import React, { PropTypes } from 'react'
import cellStyles from '../styles/cell.css'

const Cell = ({image, width, height}) => {
  return (
    <div className={cellStyles.container} style={{width: width, height: height}} >
      <img className={cellStyles.image} src={image} alt='' />
    </div>
  )
}

Cell.propTypes = {
  image: PropTypes.string.isRequired,
  width: PropTypes.string.isRequired,
  height: PropTypes.string.isRequired
}

export default Cell



