export function setVendorProperty (style, property, value) {
  style['Webkit' + property] = value
  style['Moz' + property] = value
  style['ms' + property] = value
  style['O' + property] = value
  style[property.toLowerCase()] = value
}

export function getVendorProperty (style, property) {
  return style[property.toLowerCase()] ||
        style['ms' + property] ||
        style['Webkit' + property] ||
        style['Moz' + property] ||
        style['O' + property] || ''
}

import './requestAnimationPolyfill'
export function animate ({draw, duration, timing, startPoint, endPoint}) {
  let start = window.performance.now()
  let path = (endPoint - startPoint)

  window.requestAnimationFrame(function animate (time) {
    let timeFraction = (time - start) / duration
    if (timeFraction > 1) timeFraction = 1

    let progress = timing(timeFraction)
    if (progress > 0) {
      let position = progress * path + startPoint
      draw(position)
    }

    if (timeFraction < 1) {
      window.requestAnimationFrame(animate)
    }
  })
}



