import React, {Component} from 'react'
import Carousel from '../../molecules/carousel'

let imagesData = [
  'http://images.freeimages.com/images/previews/d77/hotel-towels-1524727.jpg',
  'http://images.freeimages.com/images/previews/b1c/hotel-room-1230953.jpg',
  'http://images.freeimages.com/images/previews/d9a/breakfast-grand-hotel-1527177.jpg',
  'http://images.freeimages.com/images/previews/7c9/hotel-pool-1537428.jpg',
  'http://images.freeimages.com/images/previews/2df/hotel-1500509.jpg',
  'http://images.freeimages.com/images/previews/9d3/hotel-1217956.jpg',
  'http://images.freeimages.com/images/previews/a44/hotel-view-1577688.jpg',
  'http://images.freeimages.com/images/previews/2c4/pool-night-1521893.jpg'
]

class TravelabJmApp extends Component {
  render () {
    return (
      <Carousel zoom={false} images={imagesData} />
    )
  }
}

export default TravelabJmApp



