import React from 'react'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import {showNextImage, showPreviousImage, zoomImage} from './ducks/carousel'
import Cell from './subcomponents/Cell'
import ControlBtn from './subcomponents/ControlBtn'
import carouselStyles from './styles/index.css'
import ctrlStyles from './styles/controls.css'
import animateCarousel from './carouselAnimation'

const Carousel = ({width, height, zoom, images, showNext, showPrevious, zoomImage, animationHandler}) => {
  let contaierClasses = carouselStyles.container
  return (
    <div className={carouselStyles.carousel} style={{ width: width, height: height }} >
      <div className={contaierClasses} ref={animationHandler} >
        <Cell image={images[images.length - 1]} width={width} height={height} />
        {images.map((image, index) => {
          return (
            <Cell key={index} image={image} width={width} height={height} />
          )
        })}
        <Cell image={images[0]} width={width} height={height} />
      </div>

      <div className={ctrlStyles.container} >
        <ControlBtn value='previous' onClickHandler={showPrevious} />
        <ControlBtn value={zoom ? 'zoom' : ''} onClickHandler={zoom ? zoomImage : null} />
        <ControlBtn value='next' onClickHandler={showNext} />
      </div>
    </div>
  )
}

function mapStateToProps (state, ownProps) {
  return {
    currentImage: state.carousel.currentImage,
    width: ownProps.width || '400px',
    height: ownProps.height || '300px',
    isTransition: false
  }
}

function mapDispatchToProps (dispatch) {
  return bindActionCreators({ showNextTrigger: showNextImage,
                showPreviousTrigger: showPreviousImage,
                zoomImageTrigger: zoomImage }, dispatch)
}

function mergeProps (stateProps, dispatchProps, ownProps) {
  return {
    ...stateProps,
    ...dispatchProps,
    ...ownProps,
    showNext (e) {
      e.preventDefault()

      if (stateProps.isTransition) return

      dispatchProps.showNextTrigger({
        currentImage: stateProps.currentImage,
        imagesLength: ownProps.images.length
      })
    },
    showPrevious (e) {
      e.preventDefault()
      if (stateProps.isTransition) return

      dispatchProps.showPreviousTrigger({
        currentImage: stateProps.currentImage,
        imagesLength: ownProps.images.length
      })
    },
    zoomImage (e) {
      e.preventDefault()
      dispatchProps.zoomImageTrigger()
    },
    animationHandler (container) {
      if (!container) return

      stateProps.isTransition = true
      animateCarousel({
        element: container,
        property: 'Transform',
        durationMs: 500,
        imgIndex: stateProps.currentImage,
        imgWidth: parseInt(stateProps.width),
        imgsCount: ownProps.images.length,
        clearFlagCb: () => { stateProps.isTransition = false }
      })
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps, mergeProps)(Carousel)



