import {setVendorProperty, getVendorProperty, animate} from './utils'

export default function animateCarousel ({ element, property, durationMs = 500, imgIndex = 0,
                                          imgWidth = 400, imgsCount = 1, clearFlagCb}) {
  if (!element || !property) return

  let propertyStyle = getVendorProperty({...element.style}, property)
  if (!propertyStyle) {
    // set initial state
    let value = '-' + imgWidth + 'px'
    if (property === 'Transform') {
      value = 'translateX(-' + imgWidth + 'px)'
    }
    setVendorProperty(element.style, property, value)
    clearFlagCb()
  } else {
    let startPoint = parseInt(propertyStyle.match(/\d+px/))
    console.log(startPoint)
    let endPoint = imgIndex * imgWidth + imgWidth
    let path = endPoint - startPoint
    if (Math.abs(path) > imgWidth) {
      endPoint = path > 0 ? 0 : imgsCount * imgWidth + imgWidth
    }
    animate({
      draw: (position) => {
        if (!(position - endPoint)) {
          clearFlagCb()
        }

        if (!position) {
          position = imgsCount * imgWidth
        } else if (position === imgsCount * imgWidth + imgWidth) {
          position = imgWidth
        }

        let value = '-' + position + 'px'
        if (property === 'Transform') {
          value = 'translateX(-' + position + 'px)'
        }
        setVendorProperty(element.style, property, value)
      },
      duration: durationMs,
      timing: (time) => { return time },
      startPoint: startPoint,
      endPoint: endPoint
    })
  }
}



