import React, { PropTypes } from 'react'
import btnStyles from '../styles/controlBtn.css'

const ControlBtn = ({value, onClickHandler}) => (
  <div onClick={onClickHandler} className={btnStyles.btn + ' ' + (value === 'zoom' ? btnStyles['zoom-btn']
                                                                  : value === 'previous' ? btnStyles['previous-btn']
                                                                  : value === 'next' ? btnStyles['next-btn'] : '')} ></div>
)

ControlBtn.propTypes = {
  value: PropTypes.string.isRequired
}

export default ControlBtn



