export const SET_IMAGE = 'carousel/SET_CURRENT_IMAGE'
export const SHOW_PREVIOUS_IMAGE = 'carousel/PREVIOUS_IMAGE'
export const SHOW_NEXT_IMAGE = 'carousel/NEXT_IMAGE'
export const ZOOM_IMAGE = 'carousel/ZOOM_IMAGE'

// Reducer
const initialState = {
  currentImage: 0
}

export default function reducer (state = initialState, action) {
  switch (action.type) {
    case SET_IMAGE:
      return {...state, currentImage: action.payload}

    default:
      return state
  }
}

// actions creators
export function setImage (value) {
  return {
    type: SET_IMAGE,
    payload: value
  }
}

export function showPreviousImage (value) {
  return {
    type: SHOW_PREVIOUS_IMAGE,
    payload: value
  }
}

export function showNextImage (value) {
  return {
    type: SHOW_NEXT_IMAGE,
    payload: value
  }
}

export function zoomImage (value) {
  return {
    type: ZOOM_IMAGE,
    payload: value
  }
}



